<template>
    <div class="pl-badge">
        <slot></slot>
        <div class="pl-badge-content" :class="contentStyles">
            <slot name="badge">
                <span v-if="data!=null">{{showValue}}</span>
            </slot>
        </div>
    </div>
</template>

<script>
    export default {
        name: "pl-badge",
        props: {
            data: {},                                       //
            bottom: {type: Boolean},                        //
            start: {type: Boolean},                         //
            status: {type: String, default: 'error'},       //
            dot: {type: Boolean,},                          //
            max: {type: Number,},                           //
        },
        computed: {
            contentStyles() {
                return [
                    {
                        'pl-badge-content-dot': !!this.dot,
                    },
                    `pl-badge-content-${!!this.bottom ? 'bottom' : 'top'}`,
                    `pl-badge-content-${!!this.start ? 'start' : 'end'}`,
                    `pl-badge-content-status-${this.status}`,
                ]
            },
            showValue() {
                if (this.data == null) return null
                if (this.max != null && (this.data - 0) > this.max) return this.max + '+'
                return this.data
            },
        },
    }
</script>